@import "./normalize.css";

*:a {
  text-decoration: none;
}
.html {
  min-height: 100%;
}

body {
  background: url(./varg/images/varg.jpg);
  background-size: 100% 100%;
  height: 120vh;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  margin: 0;
  border-left: 30px solid #000;
  border-right: 30px solid #000;
  border-top: 10px solid #000;
}

.nav {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}

.nav__content {
  display: inline-flex;
  list-style: none;
  text-decoration: none;
}

.nav__item > a {
  margin-right: 5rem;
  font-size: 2rem;
  font-weight: 300;
  text-decoration: none;
  color: #fff;
  transition: color 0.3s;
  border-bottom: 5px solid #000;
}

.nav__item > a:hover {
  color: floralwhite;
}

.parents > a {
  font-size: 1.5rem;
  font-weight: 700;
  background-color: beige;
  color: #000;
  text-decoration: none;
  display: block;
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 10px;
  margin: 1rem;
  box-shadow: 0 0 30px 10px;
}

.parents {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.my-mum:hover,
.my-dad:hover {
  opacity: 0.9;
}

.footer {
  position: absolute;
  max-height: 100vh; 
  bottom: 0%;
  background: #000;
  width: 100%;
  text-align: center;
  position: fixed;
}

.footer h3 {
  color: #fff;
  font-weight: 500;
  font-size: 3rem;
  margin: 0;
}

.footer p {
  color: #fff;
  opacity: 0.7;
  margin-bottom: 0.2rem;
  margin-top: 0.1rem;
  font-size: 0.8rem;
}

@media screen and (max-width: 768px) {
  body {
    background: url(./varg/images/varg.jpg);
    background-size: auto;
  }

  .parents {
    display: flex;
    flex-direction: column;
    transform: translateY(-100px);
  }
}
